import React, { useState } from 'react'
import { Button, Dialog, DialogActions, DialogContent, DialogTitle, makeStyles } from '@material-ui/core';
import colors from '../../../assets/colors'
import SelectDropdown from '../Inputs/SelectDropdown';
import { dbUsers } from '../../Topbar/CreateChat';
import { useDispatch, useSelector } from 'react-redux';
import { RootState } from '../../../redux/reducers';
import { addTempMemberToChat, setMembersDialog, setTempMembersDialog } from '../../../redux/action/chat.action';
import { toast } from 'react-toastify';
import { getAllChats } from '../../../redux/action/chat.action';

interface AddChatMemberProps {
    
}
 
const AddTempChatMember: React.FC<AddChatMemberProps> = () => {
    const classes = useStyles();
    const { tempMembersDialog, selectedChat, chat } = useSelector((state: RootState) => state.chat);
    const chatMembers = chat?.find((room: any) => room._id === selectedChat)?.members || [];
    const memberIds = chatMembers?.map((member: any) => member.id);
    const availableUsers = dbUsers.filter((user: any) => !memberIds.includes(user.value)) || [];
    const dispatch = useDispatch();
    const [selectedUser, setSelectedUser] = useState<any>();

    const handleOk = () => {
        dispatch(addTempMemberToChat({
            other: {
                roomId: selectedChat,
                userId: selectedUser?.value
            },
            success: () => {
                dispatch(getAllChats());
                toast.success('New member added successfully');   
                setSelectedUser(null);
            }
        }));
        handleClose();
    }

    const handleClose = () => {
        dispatch(setTempMembersDialog(false));
    }

    return (

        <Dialog
            open={tempMembersDialog}
            onClose={handleClose}
        >
            <DialogTitle id="alert-dialog-title">{"Select a user"}</DialogTitle>
            <DialogContent>
                <div className={classes.dropdownWrapper}>
                    <SelectDropdown
                        title="User"
                        placeholder="Please select a user"
                        data={availableUsers}
                        value={selectedUser}
                        handleChange={(e: any) => setSelectedUser(e)}
                    />
                </div>
            </DialogContent>
            <DialogActions>
            <Button disabled={!selectedUser}  onClick={handleOk} color="primary">
                ok
            </Button>
            <Button onClick={handleClose} color="secondary" autoFocus>
                cancel
            </Button>
            </DialogActions>
        </Dialog>

    );
}
 
export default AddTempChatMember;

const useStyles = makeStyles({
    menuWrapper: {
        display: 'flex',
        alignItems: 'baseline',
        justifyContent: 'flex-start'
    },
    menuText: {
        fontSize: 14,
        fontWeight: 500,
        marginLeft: 10,
        height: 30,
        color: colors.textPrimary
    },
    dropdownWrapper: {
        maxWidth: 300,
        width: 300,
        height: 300
    }
})